<template>
  <v-layout>
    <v-card contextual-style="dark">
      <span slot="header">
        Character
      </span>
      <div slot="body">
        <form ref="characterForm" novalidate @submit.prevent="upload()">
          <div class="form-group">
            <div class="input-group">
              <input
                required="true"
                type="text"
                placeholder="Name"
                class="form-control"
                v-model="image.name"
              >
            </div>
          </div>
          <div class="form-group">
            <div class="input-group">
              <input
                required="true"
                type="text"
                placeholder="Show"
                class="form-control"
                v-model="image.show"
              >
            </div>
          </div>
          <div class="form-group">
            <div class="input-group">
              <textarea class="form-control" rows="2" id="bio" placeholder="Bio" v-model="image.bio"></textarea>
            </div>
          </div>
          <div class="form-group">
            <div class="input-group">
              <input
                required="true"
                type="text"
                placeholder="File name"
                class="form-control"
                v-model="image.fileName"
              >
            </div>
          </div>
          <div class="form-group">
            <div class="input-group">
              <input type="file" v-on:change="onFileChange" class="form-control">
            </div>
          </div>
          <!-- <div class="form-group"> -->
            
          <!-- </div> -->
          <div class="form-group">
            <button class="btn custom-button">
              Submit
              <!-- <i v-if="$store.state.auth.loading" class="fa fa-spinner fa-spin"></i> -->
            </button>
          </div>
        </form>
        <img v-if="image.data" :src="image.data" style="width:50%" class="img-responsive center-block">
      </div>
    </v-card>
  </v-layout>
</template>

<script>
  /* ============
   * Home Index Page
   * ============
   *
   * The home index page.
   */

  import VLayout from '@/layouts/Default';
  import VCard from '@/components/Card';
  import CharacterProxy from '@/proxies/CharacterProxy';

  export default {
    /**
     * The name of the page.
     */
    name: 'admin-character-index',

    data() {
      return {
        image: {
          data: '',
          name: null,
          show: null,
          bio: null,
          fileName: null,
        },
      };
    },

    /**
     * The methods the page can use.
     */
    methods: {
      onFileChange(e) {
        const files = e.target.files || e.dataTransfer.files;
        if (!files.length) {
          return;
        }
        this.createImage(files[0]);
      },
      createImage(file) {
        const reader = new FileReader();
        const vm = this;
        reader.onload = (e) => {
          vm.image.data = e.target.result;
        };
        reader.readAsDataURL(file);
      },
      upload() {
        new CharacterProxy().upload(this.image).then((response) => {
          console.log(response);
        })
        .catch(() => {
          console.log('Request failed...');
        });
      },
    },

    /**
     * The components that the page can use.
     */
    components: {
      VLayout,
      VCard,
    },
  };
</script>



